'use client'

import { useState, useEffect, useRef, useCallback } from 'react'
import type { Player, PlayerResponse, PlayerSuggestion, PlayerSearchResponse } from '@/types/player'
import type { PlayerComparisonAnalysis } from '@/types/ai-prediction'
import { logQuery } from '@/utils/queryLogger'
import AskAIFollowUp from '@/components/AskAIFollowUp'
import AICautionBanner from '@/components/AICautionBanner'

export default function PlayerComparisonPage() {
  // Player 1 state
  const [player1Query, setPlayer1Query] = useState('')
  const [player1, setPlayer1] = useState<Player | null>(null)
  const [player1Suggestions, setPlayer1Suggestions] = useState<PlayerSuggestion[]>([])
  const [showPlayer1Suggestions, setShowPlayer1Suggestions] = useState(false)
  const [loadingPlayer1Suggestions, setLoadingPlayer1Suggestions] = useState(false)
  const [selectedPlayer1Index, setSelectedPlayer1Index] = useState(-1)

  // Player 2 state
  const [player2Query, setPlayer2Query] = useState('')
  const [player2, setPlayer2] = useState<Player | null>(null)
  const [player2Suggestions, setPlayer2Suggestions] = useState<PlayerSuggestion[]>([])
  const [showPlayer2Suggestions, setShowPlayer2Suggestions] = useState(false)
  const [loadingPlayer2Suggestions, setLoadingPlayer2Suggestions] = useState(false)
  const [selectedPlayer2Index, setSelectedPlayer2Index] = useState(-1)

  // General state
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  // AI Comparison state
  const [aiAnalysis, setAiAnalysis] = useState<PlayerComparisonAnalysis | null>(null)
  const [loadingAI, setLoadingAI] = useState(false)
  const [aiError, setAiError] = useState<string | null>(null)

  // Refs
  const player1InputRef = useRef<HTMLInputElement>(null)
  const player1DropdownRef = useRef<HTMLDivElement>(null)
  const player2InputRef = useRef<HTMLInputElement>(null)
  const player2DropdownRef = useRef<HTMLDivElement>(null)
  const debounceTimer1Ref = useRef<NodeJS.Timeout | null>(null)
  const debounceTimer2Ref = useRef<NodeJS.Timeout | null>(null)

  // Fetch suggestions for Player 1
  const fetchPlayer1Suggestions = useCallback(async (query: string) => {
    if (!query.trim() || query.trim().length < 2) {
      setPlayer1Suggestions([])
      setShowPlayer1Suggestions(false)
      return
    }

    setLoadingPlayer1Suggestions(true)
    try {
      const response = await fetch(`/api/player-search?query=${encodeURIComponent(query)}&limit=8`)
      const data: PlayerSearchResponse = await response.json()

      if (response.ok && data.success) {
        setPlayer1Suggestions(data.data)
        setShowPlayer1Suggestions(data.data.length > 0)
      } else {
        setPlayer1Suggestions([])
        setShowPlayer1Suggestions(false)
      }
    } catch (err) {
      console.error('Error fetching Player 1 suggestions:', err)
      setPlayer1Suggestions([])
      setShowPlayer1Suggestions(false)
    } finally {
      setLoadingPlayer1Suggestions(false)
    }
  }, [])

  // Fetch suggestions for Player 2
  const fetchPlayer2Suggestions = useCallback(async (query: string) => {
    if (!query.trim() || query.trim().length < 2) {
      setPlayer2Suggestions([])
      setShowPlayer2Suggestions(false)
      return
    }

    setLoadingPlayer2Suggestions(true)
    try {
      const response = await fetch(`/api/player-search?query=${encodeURIComponent(query)}&limit=8`)
      const data: PlayerSearchResponse = await response.json()

      if (response.ok && data.success) {
        setPlayer2Suggestions(data.data)
        setShowPlayer2Suggestions(data.data.length > 0)
      } else {
        setPlayer2Suggestions([])
        setShowPlayer2Suggestions(false)
      }
    } catch (err) {
      console.error('Error fetching Player 2 suggestions:', err)
      setPlayer2Suggestions([])
      setShowPlayer2Suggestions(false)
    } finally {
      setLoadingPlayer2Suggestions(false)
    }
  }, [])

  // Debounced input handlers
  const handlePlayer1InputChange = (value: string) => {
    setPlayer1Query(value)
    setSelectedPlayer1Index(-1)

    if (debounceTimer1Ref.current) {
      clearTimeout(debounceTimer1Ref.current)
    }

    debounceTimer1Ref.current = setTimeout(() => {
      fetchPlayer1Suggestions(value)
    }, 300)
  }

  const handlePlayer2InputChange = (value: string) => {
    setPlayer2Query(value)
    setSelectedPlayer2Index(-1)

    if (debounceTimer2Ref.current) {
      clearTimeout(debounceTimer2Ref.current)
    }

    debounceTimer2Ref.current = setTimeout(() => {
      fetchPlayer2Suggestions(value)
    }, 300)
  }

  // Select Player 1
  const selectPlayer1 = (suggestion: PlayerSuggestion) => {
    setPlayer1Query(suggestion.full_name)
    setShowPlayer1Suggestions(false)
    setPlayer1Suggestions([])
    setSelectedPlayer1Index(-1)
    fetchPlayerStats(suggestion.full_name, 1)
  }

  // Select Player 2
  const selectPlayer2 = (suggestion: PlayerSuggestion) => {
    setPlayer2Query(suggestion.full_name)
    setShowPlayer2Suggestions(false)
    setPlayer2Suggestions([])
    setSelectedPlayer2Index(-1)
    fetchPlayerStats(suggestion.full_name, 2)
  }

  // Fetch player stats
  const fetchPlayerStats = async (playerName: string, playerNumber: 1 | 2) => {
    setLoading(true)
    setError(null)

    try {
      const response = await fetch(`/api/player?name=${encodeURIComponent(playerName)}`)
      const data: PlayerResponse = await response.json()

      if (!response.ok || !data.success) {
        throw new Error((data as any).error || 'Failed to fetch player data')
      }

      if (data.data.length === 0) {
        throw new Error('Player not found')
      }

      if (playerNumber === 1) {
        setPlayer1(data.data[0])
      } else {
        setPlayer2(data.data[0])
      }

      // Clear previous AI analysis when selecting new player
      setAiAnalysis(null)
      setAiError(null)
      
      // Log comparison when both players are loaded
      const otherPlayer = playerNumber === 1 ? player2 : player1
      const currentPlayer = data.data[0]
      
      if (otherPlayer && currentPlayer) {
        const p1 = playerNumber === 1 ? currentPlayer : otherPlayer
        const p2 = playerNumber === 1 ? otherPlayer : currentPlayer
        
        logQuery({
          query_type: 'Player Comparison',
          details: {
            timestamp: new Date().toISOString(),
            player1: {
              name: p1.full_name,
              id: p1.id,
              team: p1.team.name
            },
            player2: {
              name: p2.full_name,
              id: p2.id,
              team: p2.team.name
            },
            season: p1.recent_season?.season || '2024-25',
            comparisonStats: {
              points: { player1: p1.headline_stats.pts, player2: p2.headline_stats.pts },
              assists: { player1: p1.headline_stats.ast, player2: p2.headline_stats.ast },
              rebounds: { player1: p1.headline_stats.reb, player2: p2.headline_stats.reb }
            }
          }
        })
      }
    } catch (err: any) {
      setError(err.message)
      if (playerNumber === 1) {
        setPlayer1(null)
      } else {
        setPlayer2(null)
      }
    } finally {
      setLoading(false)
    }
  }

  // Fetch AI comparison analysis
  const fetchAIComparison = async () => {
    if (!player1 || !player2) return

    setLoadingAI(true)
    setAiError(null)

    try {
      const response = await fetch('/api/ai-comparison', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          player1,
          player2
        })
      })

      const data = await response.json()

      if (!response.ok || !data.success) {
        throw new Error(data.error || 'Failed to generate AI comparison')
      }

      setAiAnalysis(data.analysis)
    } catch (err: any) {
      console.error('Error fetching AI comparison:', err)
      setAiError(err.message || 'Failed to generate AI comparison')
    } finally {
      setLoadingAI(false)
    }
  }

  // Trigger AI comparison when both players are loaded
  useEffect(() => {
    if (player1 && player2 && !loading) {
      fetchAIComparison()
    }
  }, [player1, player2, loading])

  // Keyboard navigation for Player 1
  const handlePlayer1KeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (!showPlayer1Suggestions || player1Suggestions.length === 0) return

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault()
        setSelectedPlayer1Index(prev =>
          prev < player1Suggestions.length - 1 ? prev + 1 : prev
        )
        break
      case 'ArrowUp':
        e.preventDefault()
        setSelectedPlayer1Index(prev => prev > 0 ? prev - 1 : -1)
        break
      case 'Enter':
        e.preventDefault()
        if (selectedPlayer1Index >= 0 && selectedPlayer1Index < player1Suggestions.length) {
          selectPlayer1(player1Suggestions[selectedPlayer1Index])
        }
        break
      case 'Escape':
        setShowPlayer1Suggestions(false)
        setSelectedPlayer1Index(-1)
        break
    }
  }

  // Keyboard navigation for Player 2
  const handlePlayer2KeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (!showPlayer2Suggestions || player2Suggestions.length === 0) return

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault()
        setSelectedPlayer2Index(prev =>
          prev < player2Suggestions.length - 1 ? prev + 1 : prev
        )
        break
      case 'ArrowUp':
        e.preventDefault()
        setSelectedPlayer2Index(prev => prev > 0 ? prev - 1 : -1)
        break
      case 'Enter':
        e.preventDefault()
        if (selectedPlayer2Index >= 0 && selectedPlayer2Index < player2Suggestions.length) {
          selectPlayer2(player2Suggestions[selectedPlayer2Index])
        }
        break
      case 'Escape':
        setShowPlayer2Suggestions(false)
        setSelectedPlayer2Index(-1)
        break
    }
  }

  // Click outside handlers
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      // Player 1
      if (
        player1DropdownRef.current &&
        !player1DropdownRef.current.contains(event.target as Node) &&
        player1InputRef.current &&
        !player1InputRef.current.contains(event.target as Node)
      ) {
        setShowPlayer1Suggestions(false)
        setSelectedPlayer1Index(-1)
      }

      // Player 2
      if (
        player2DropdownRef.current &&
        !player2DropdownRef.current.contains(event.target as Node) &&
        player2InputRef.current &&
        !player2InputRef.current.contains(event.target as Node)
      ) {
        setShowPlayer2Suggestions(false)
        setSelectedPlayer2Index(-1)
      }
    }

    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])

  // Cleanup timers
  useEffect(() => {
    return () => {
      if (debounceTimer1Ref.current) clearTimeout(debounceTimer1Ref.current)
      if (debounceTimer2Ref.current) clearTimeout(debounceTimer2Ref.current)
    }
  }, [])

  const formatPercentage = (value: number | null | undefined) => {
    if (value === null || value === undefined) return 'N/A'
    return `${(value * 100).toFixed(1)}%`
  }

  const formatNumber = (value: number | null | undefined) => {
    if (value === null || value === undefined) return 'N/A'
    return value.toFixed(1)
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-2xl font-bold text-foreground">Player Comparison</h1>
        <p className="text-muted-foreground mt-1">Compare two NBA players side by side</p>
      </div>

      {/* AI Caution Banner */}
      <AICautionBanner />

      {/* Player Selection Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Player 1 Selection */}
        <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md p-6 border border-border/50">
          <h3 className="text-lg font-semibold text-foreground mb-4">Player 1</h3>
          <div className="relative">
            <input
              ref={player1InputRef}
              type="text"
              placeholder="Search for first player..."
              value={player1Query}
              onChange={(e) => handlePlayer1InputChange(e.target.value)}
              onKeyDown={handlePlayer1KeyDown}
              onFocus={() => {
                if (player1Suggestions.length > 0) {
                  setShowPlayer1Suggestions(true)
                }
              }}
              className="w-full px-4 py-3 bg-secondary/50 border border-border text-foreground rounded-lg focus:outline-none focus:ring-2 focus:ring-primary placeholder:text-muted-foreground"
              autoComplete="off"
            />

            {/* Player 1 Autocomplete Dropdown */}
            {showPlayer1Suggestions && (player1Suggestions.length > 0 || loadingPlayer1Suggestions) && (
              <div
                ref={player1DropdownRef}
                className="absolute z-50 w-full mt-2 bg-card border border-border rounded-lg shadow-lg max-h-96 overflow-y-auto custom-scrollbar"
              >
                {loadingPlayer1Suggestions ? (
                  <div className="p-4 text-center text-muted-foreground">
                    <div className="inline-block animate-spin rounded-full h-5 w-5 border-b-2 border-primary mr-2"></div>
                    Loading...
                  </div>
                ) : (
                  <ul className="py-2">
                    {player1Suggestions.map((suggestion, index) => (
                      <li key={suggestion.id}>
                        <button
                          type="button"
                          onClick={() => selectPlayer1(suggestion)}
                          className={`w-full px-4 py-3 flex items-center gap-3 hover:bg-secondary/50 transition-colors ${
                            index === selectedPlayer1Index ? 'bg-secondary/50' : ''
                          }`}
                        >
                          <div className="w-10 h-10 flex-shrink-0">
                            <img
                              src={suggestion.headshot_url}
                              alt={suggestion.full_name}
                              className="w-full h-full rounded-full object-cover bg-gray-100"
                              onError={(e) => {
                                const target = e.target as HTMLImageElement
                                target.style.display = 'none'
                                const parent = target.parentElement
                                if (parent) {
                                  parent.innerHTML = `
                                    <div class="w-full h-full bg-gradient-to-br from-blue-600 to-blue-800 rounded-full flex items-center justify-center text-white font-bold text-xs">
                                      ${suggestion.first_name.charAt(0)}${suggestion.last_name.charAt(0)}
                                    </div>
                                  `
                                }
                              }}
                            />
                          </div>
                          <div className="flex-1 text-left">
                            <div className="font-semibold text-foreground text-sm">
                              {suggestion.full_name}
                            </div>
                            <div className="text-xs text-muted-foreground">
                              {suggestion.is_active ? (
                                <span className="text-accent font-medium">● Active</span>
                              ) : (
                                <span className="text-muted-foreground">Inactive</span>
                              )}
                            </div>
                          </div>
                        </button>
                      </li>
                    ))}
                  </ul>
                )}
              </div>
            )}
          </div>

          {player1 && (
            <div className="mt-4 p-4 bg-primary/10 rounded-lg border border-primary/20">
              <div className="flex items-center gap-3">
                <div className="w-12 h-12">
                  <img
                    src={`https://cdn.nba.com/headshots/nba/latest/260x190/${player1.id}.png`}
                    alt={player1.full_name}
                    className="w-full h-full rounded-lg object-cover"
                    onError={(e) => {
                      const target = e.target as HTMLImageElement
                      target.style.display = 'none'
                      const parent = target.parentElement
                      if (parent) {
                        parent.innerHTML = `
                          <div class="w-full h-full bg-gradient-to-br from-blue-600 to-blue-800 rounded-lg flex items-center justify-center text-white font-bold">
                            ${player1.first_name.charAt(0)}${player1.last_name.charAt(0)}
                          </div>
                        `
                      }
                    }}
                  />
                </div>
                <div>
                  <div className="font-bold text-foreground">{player1.full_name}</div>
                  <div className="text-sm text-muted-foreground">{player1.team.name}</div>
                </div>
              </div>
            </div>
          )}
        </div>

        {/* Player 2 Selection */}
        <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-6">
          <h3 className="text-lg font-semibold text-foreground mb-4">Player 2</h3>
          <div className="relative">
            <input
              ref={player2InputRef}
              type="text"
              placeholder="Search for second player..."
              value={player2Query}
              onChange={(e) => handlePlayer2InputChange(e.target.value)}
              onKeyDown={handlePlayer2KeyDown}
              onFocus={() => {
                if (player2Suggestions.length > 0) {
                  setShowPlayer2Suggestions(true)
                }
              }}
              className="w-full px-4 py-3 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-accent"
              autoComplete="off"
            />

            {/* Player 2 Autocomplete Dropdown */}
            {showPlayer2Suggestions && (player2Suggestions.length > 0 || loadingPlayer2Suggestions) && (
              <div
                ref={player2DropdownRef}
                className="absolute z-50 w-full mt-2 bg-card border border-border rounded-lg shadow-lg max-h-96 overflow-y-auto custom-scrollbar"
              >
                {loadingPlayer2Suggestions ? (
                  <div className="p-4 text-center text-muted-foreground">
                    <div className="inline-block animate-spin rounded-full h-5 w-5 border-b-2 border-accent mr-2"></div>
                    Loading...
                  </div>
                ) : (
                  <ul className="py-2">
                    {player2Suggestions.map((suggestion, index) => (
                      <li key={suggestion.id}>
                        <button
                          type="button"
                          onClick={() => selectPlayer2(suggestion)}
                          className={`w-full px-4 py-3 flex items-center gap-3 hover:bg-accent/10 transition-colors ${
                            index === selectedPlayer2Index ? 'bg-accent/10' : ''
                          }`}
                        >
                          <div className="w-10 h-10 flex-shrink-0">
                            <img
                              src={suggestion.headshot_url}
                              alt={suggestion.full_name}
                              className="w-full h-full rounded-full object-cover bg-gray-100"
                              onError={(e) => {
                                const target = e.target as HTMLImageElement
                                target.style.display = 'none'
                                const parent = target.parentElement
                                if (parent) {
                                  parent.innerHTML = `
                                    <div class="w-full h-full bg-gradient-to-br from-green-600 to-green-800 rounded-full flex items-center justify-center text-white font-bold text-xs">
                                      ${suggestion.first_name.charAt(0)}${suggestion.last_name.charAt(0)}
                                    </div>
                                  `
                                }
                              }}
                            />
                          </div>
                          <div className="flex-1 text-left">
                            <div className="font-semibold text-foreground text-sm">
                              {suggestion.full_name}
                            </div>
                            <div className="text-xs text-muted-foreground">
                              {suggestion.is_active ? (
                                <span className="text-accent font-medium">● Active</span>
                              ) : (
                                <span className="text-muted-foreground">Inactive</span>
                              )}
                            </div>
                          </div>
                        </button>
                      </li>
                    ))}
                  </ul>
                )}
              </div>
            )}
          </div>

          {player2 && (
            <div className="mt-4 p-4 bg-accent/10 rounded-lg">
              <div className="flex items-center gap-3">
                <div className="w-12 h-12">
                  <img
                    src={`https://cdn.nba.com/headshots/nba/latest/260x190/${player2.id}.png`}
                    alt={player2.full_name}
                    className="w-full h-full rounded-lg object-cover"
                    onError={(e) => {
                      const target = e.target as HTMLImageElement
                      target.style.display = 'none'
                      const parent = target.parentElement
                      if (parent) {
                        parent.innerHTML = `
                          <div class="w-full h-full bg-gradient-to-br from-green-600 to-green-800 rounded-lg flex items-center justify-center text-white font-bold">
                            ${player2.first_name.charAt(0)}${player2.last_name.charAt(0)}
                          </div>
                        `
                      }
                    }}
                  />
                </div>
                <div>
                  <div className="font-bold text-foreground">{player2.full_name}</div>
                  <div className="text-sm text-muted-foreground">{player2.team.name}</div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Error Message */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4">
          <div className="flex items-center gap-2">
            <span className="text-red-600 text-xl">⚠️</span>
            <p className="text-red-800 font-medium">{error}</p>
          </div>
        </div>
      )}

      {/* Loading State */}
      {loading && (
        <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-12">
          <div className="flex flex-col items-center justify-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mb-4"></div>
            <p className="text-muted-foreground">Loading player stats...</p>
          </div>
        </div>
      )}

      {/* Comparison Display */}
      {player1 && player2 && !loading && (
        <div className="space-y-6">
          {/* Player Headers Comparison */}
          <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 overflow-hidden">
            <div className="grid grid-cols-2 divide-x divide-gray-200">
              {/* Player 1 Header */}
              <div className="p-6 bg-gradient-to-br from-blue-50 to-blue-100">
                <div className="flex items-center gap-4">
                  <div className="w-20 h-20">
                    <img
                      src={`https://cdn.nba.com/headshots/nba/latest/260x190/${player1.id}.png`}
                      alt={player1.full_name}
                      className="w-full h-full rounded-lg object-cover border-2 border-blue-200"
                      onError={(e) => {
                        const target = e.target as HTMLImageElement
                        target.style.display = 'none'
                        const parent = target.parentElement
                        if (parent) {
                          parent.innerHTML = `
                            <div class="w-full h-full bg-gradient-to-br from-blue-600 to-blue-800 rounded-lg flex items-center justify-center text-white font-bold text-2xl border-2 border-blue-200">
                              ${player1.first_name.charAt(0)}${player1.last_name.charAt(0)}
                            </div>
                          `
                        }
                      }}
                    />
                  </div>
                  <div>
                    <h3 className="text-xl font-bold text-foreground">{player1.full_name}</h3>
                    <p className="text-sm text-muted-foreground">{player1.team.name}</p>
                    <div className="flex items-center gap-2 mt-1">
                      <span className="text-xs bg-blue-200 text-blue-800 px-2 py-1 rounded-full font-medium">
                        {player1.position}
                      </span>
                      <span className="text-xs bg-gray-200 text-gray-800 px-2 py-1 rounded-full">
                        #{player1.jersey}
                      </span>
                    </div>
                  </div>
                </div>
              </div>

              {/* Player 2 Header */}
              <div className="p-6 bg-gradient-to-br from-green-50 to-green-100">
                <div className="flex items-center gap-4">
                  <div className="w-20 h-20">
                    <img
                      src={`https://cdn.nba.com/headshots/nba/latest/260x190/${player2.id}.png`}
                      alt={player2.full_name}
                      className="w-full h-full rounded-lg object-cover border-2 border-green-200"
                      onError={(e) => {
                        const target = e.target as HTMLImageElement
                        target.style.display = 'none'
                        const parent = target.parentElement
                        if (parent) {
                          parent.innerHTML = `
                            <div class="w-full h-full bg-gradient-to-br from-green-600 to-green-800 rounded-lg flex items-center justify-center text-white font-bold text-2xl border-2 border-green-200">
                              ${player2.first_name.charAt(0)}${player2.last_name.charAt(0)}
                            </div>
                          `
                        }
                      }}
                    />
                  </div>
                  <div>
                    <h3 className="text-xl font-bold text-foreground">{player2.full_name}</h3>
                    <p className="text-sm text-muted-foreground">{player2.team.name}</p>
                    <div className="flex items-center gap-2 mt-1">
                      <span className="text-xs bg-green-200 text-green-800 px-2 py-1 rounded-full font-medium">
                        {player2.position}
                      </span>
                      <span className="text-xs bg-gray-200 text-gray-800 px-2 py-1 rounded-full">
                        #{player2.jersey}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Physical Stats */}
          <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 overflow-hidden">
            <div className="bg-secondary/30 px-6 py-3 border-b border-border/50">
              <h4 className="font-semibold text-foreground">Physical Attributes</h4>
            </div>
            <div className="grid grid-cols-3 divide-x divide-gray-200">
              <div className="p-4 text-center bg-primary/10">
                <div className="text-sm text-muted-foreground mb-1">Height</div>
                <div className="font-bold text-foreground">{player1.height}</div>
              </div>
              <div className="p-4 text-center">
                <div className="text-xs text-muted-foreground uppercase font-medium">Stat</div>
              </div>
              <div className="p-4 text-center bg-accent/10">
                <div className="text-sm text-muted-foreground mb-1">Height</div>
                <div className="font-bold text-foreground">{player2.height}</div>
              </div>
            </div>
            <div className="grid grid-cols-3 divide-x divide-gray-200 border-t border-border/50">
              <div className="p-4 text-center bg-primary/10">
                <div className="text-sm text-muted-foreground mb-1">Weight</div>
                <div className="font-bold text-foreground">{player1.weight} lbs</div>
              </div>
              <div className="p-4 text-center">
                <div className="text-xs text-muted-foreground uppercase font-medium">Weight</div>
              </div>
              <div className="p-4 text-center bg-accent/10">
                <div className="text-sm text-muted-foreground mb-1">Weight</div>
                <div className="font-bold text-foreground">{player2.weight} lbs</div>
              </div>
            </div>
          </div>

          {/* Career Stats Comparison */}
          <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 overflow-hidden">
            <div className="bg-secondary/30 px-6 py-3 border-b border-border/50">
              <h4 className="font-semibold text-foreground">Career Statistics (Per Game)</h4>
            </div>
            <table className="w-full">
              <tbody className="divide-y divide-gray-200">
                {[
                  { label: 'Games Played', key: 'games_played' as const },
                  { label: 'Points', key: 'points' as const },
                  { label: 'Assists', key: 'assists' as const },
                  { label: 'Rebounds', key: 'rebounds' as const },
                  { label: 'Steals', key: 'steals' as const },
                  { label: 'Blocks', key: 'blocks' as const },
                ].map((stat) => (
                  <tr key={stat.key}>
                    <td className="p-4 text-right bg-primary/10 font-semibold text-foreground">
                      {formatNumber(player1.career_stats[stat.key])}
                    </td>
                    <td className="p-4 text-center text-sm text-muted-foreground font-medium">
                      {stat.label}
                    </td>
                    <td className="p-4 text-left bg-accent/10 font-semibold text-foreground">
                      {formatNumber(player2.career_stats[stat.key])}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {/* Shooting Stats */}
          <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 overflow-hidden">
            <div className="bg-secondary/30 px-6 py-3 border-b border-border/50">
              <h4 className="font-semibold text-foreground">Shooting Percentages</h4>
            </div>
            <table className="w-full">
              <tbody className="divide-y divide-gray-200">
                {[
                  { label: 'Field Goal %', key: 'field_goal_pct' as const },
                  { label: '3-Point %', key: 'three_point_pct' as const },
                  { label: 'Free Throw %', key: 'free_throw_pct' as const },
                ].map((stat) => (
                  <tr key={stat.key}>
                    <td className="p-4 text-right bg-primary/10 font-semibold text-foreground">
                      {formatPercentage(player1.career_stats[stat.key])}
                    </td>
                    <td className="p-4 text-center text-sm text-muted-foreground font-medium">
                      {stat.label}
                    </td>
                    <td className="p-4 text-left bg-accent/10 font-semibold text-foreground">
                      {formatPercentage(player2.career_stats[stat.key])}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {/* AI-Powered Comparison Analysis */}
          {loadingAI && (
            <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-8">
              <div className="flex flex-col items-center justify-center">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-purple-600 mb-4"></div>
                <p className="text-foreground font-semibold">🤖 AI is analyzing the comparison...</p>
                <p className="text-muted-foreground text-sm mt-2">This may take a few seconds</p>
              </div>
            </div>
          )}

          {aiError && (
            <div className="bg-destructive/10 border-2 border-destructive/30 rounded-lg p-6">
              <div className="flex items-start gap-3">
                <span className="text-2xl">⚠️</span>
                <div>
                  <h4 className="font-semibold text-destructive mb-1">AI Analysis Error</h4>
                  <p className="text-destructive text-sm">{aiError}</p>
                  <button
                    onClick={fetchAIComparison}
                    className="mt-3 px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors text-sm font-medium"
                  >
                    Retry Analysis
                  </button>
                </div>
              </div>
            </div>
          )}

          {aiAnalysis && !loadingAI && (
            <div className="space-y-4">
              {/* Overall Verdict Card */}
              <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md overflow-hidden border border-border/50">
                <div className="bg-gradient-to-r from-purple-600 to-indigo-600 px-6 py-4">
                  <div className="flex items-center gap-3">
                    <span className="text-3xl">🤖</span>
                    <h3 className="text-xl font-bold text-white">AI-Powered Comparison Analysis</h3>
                  </div>
                </div>
                
                <div className="p-6 space-y-4">
                  {/* Overall Verdict */}
                  <div className="bg-secondary/30 rounded-lg p-4 shadow-sm border border-border/30">
                    <div className="flex items-start justify-between mb-2">
                      <h4 className="font-semibold text-foreground text-lg">Overall Verdict</h4>
                      <span className={`px-3 py-1 rounded-full text-xs font-medium ${
                        aiAnalysis.confidence === 'High' ? 'bg-green-100 text-green-800' :
                        aiAnalysis.confidence === 'Medium' ? 'bg-yellow-100 text-yellow-800' :
                        'bg-gray-100 text-gray-800'
                      }`}>
                        {aiAnalysis.confidence} Confidence
                      </span>
                    </div>
                    <p className="text-foreground leading-relaxed">{aiAnalysis.verdict}</p>
                  </div>

                  {/* Category Comparisons Grid */}
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {/* Scoring */}
                    <div className="bg-secondary/30 rounded-lg p-4 shadow-sm border border-border/30">
                      <div className="flex items-center justify-between mb-2">
                        <h5 className="font-semibold text-foreground flex items-center gap-2">
                          <span>🏀</span> Scoring
                        </h5>
                        <span className={`text-xs font-medium px-2 py-1 rounded ${
                          aiAnalysis.scoringComparison.winner === 'player1' ? 'bg-blue-100 text-blue-800' :
                          aiAnalysis.scoringComparison.winner === 'player2' ? 'bg-green-100 text-green-800' :
                          'bg-gray-100 text-gray-800'
                        }`}>
                          {aiAnalysis.scoringComparison.winner === 'player1' ? player1?.full_name :
                           aiAnalysis.scoringComparison.winner === 'player2' ? player2?.full_name : 'Tie'}
                        </span>
                      </div>
                      <div className="flex justify-between items-center mb-2">
                        <span className="text-sm text-muted-foreground">{player1?.full_name}</span>
                        <span className="font-bold text-blue-600">{aiAnalysis.scoringComparison.player1Score.toFixed(1)} PPG</span>
                      </div>
                      <div className="flex justify-between items-center mb-2">
                        <span className="text-sm text-muted-foreground">{player2?.full_name}</span>
                        <span className="font-bold text-green-600">{aiAnalysis.scoringComparison.player2Score.toFixed(1)} PPG</span>
                      </div>
                      <p className="text-xs text-muted-foreground mt-2 italic">{aiAnalysis.scoringComparison.reasoning}</p>
                    </div>

                    {/* Rebounding */}
                    <div className="bg-secondary/30 rounded-lg p-4 shadow-sm border border-border/30">
                      <div className="flex items-center justify-between mb-2">
                        <h5 className="font-semibold text-foreground flex items-center gap-2">
                          <span>📊</span> Rebounding
                        </h5>
                        <span className={`text-xs font-medium px-2 py-1 rounded ${
                          aiAnalysis.reboundingComparison.winner === 'player1' ? 'bg-blue-100 text-blue-800' :
                          aiAnalysis.reboundingComparison.winner === 'player2' ? 'bg-green-100 text-green-800' :
                          'bg-gray-100 text-gray-800'
                        }`}>
                          {aiAnalysis.reboundingComparison.winner === 'player1' ? player1?.full_name :
                           aiAnalysis.reboundingComparison.winner === 'player2' ? player2?.full_name : 'Tie'}
                        </span>
                      </div>
                      <div className="flex justify-between items-center mb-2">
                        <span className="text-sm text-muted-foreground">{player1?.full_name}</span>
                        <span className="font-bold text-blue-600">{aiAnalysis.reboundingComparison.player1Rebounds.toFixed(1)} RPG</span>
                      </div>
                      <div className="flex justify-between items-center mb-2">
                        <span className="text-sm text-muted-foreground">{player2?.full_name}</span>
                        <span className="font-bold text-green-600">{aiAnalysis.reboundingComparison.player2Rebounds.toFixed(1)} RPG</span>
                      </div>
                      <p className="text-xs text-muted-foreground mt-2 italic">{aiAnalysis.reboundingComparison.reasoning}</p>
                    </div>

                    {/* Playmaking */}
                    <div className="bg-secondary/30 rounded-lg p-4 shadow-sm border border-border/30">
                      <div className="flex items-center justify-between mb-2">
                        <h5 className="font-semibold text-foreground flex items-center gap-2">
                          <span>🎯</span> Playmaking
                        </h5>
                        <span className={`text-xs font-medium px-2 py-1 rounded ${
                          aiAnalysis.playmaking.winner === 'player1' ? 'bg-blue-100 text-blue-800' :
                          aiAnalysis.playmaking.winner === 'player2' ? 'bg-green-100 text-green-800' :
                          'bg-gray-100 text-gray-800'
                        }`}>
                          {aiAnalysis.playmaking.winner === 'player1' ? player1?.full_name :
                           aiAnalysis.playmaking.winner === 'player2' ? player2?.full_name : 'Tie'}
                        </span>
                      </div>
                      <div className="flex justify-between items-center mb-2">
                        <span className="text-sm text-muted-foreground">{player1?.full_name}</span>
                        <span className="font-bold text-blue-600">{aiAnalysis.playmaking.player1Assists.toFixed(1)} APG</span>
                      </div>
                      <div className="flex justify-between items-center mb-2">
                        <span className="text-sm text-muted-foreground">{player2?.full_name}</span>
                        <span className="font-bold text-green-600">{aiAnalysis.playmaking.player2Assists.toFixed(1)} APG</span>
                      </div>
                      <p className="text-xs text-muted-foreground mt-2 italic">{aiAnalysis.playmaking.reasoning}</p>
                    </div>

                    {/* Defense */}
                    <div className="bg-secondary/30 rounded-lg p-4 shadow-sm border border-border/30">
                      <div className="flex items-center justify-between mb-2">
                        <h5 className="font-semibold text-foreground flex items-center gap-2">
                          <span>🛡️</span> Defense
                        </h5>
                        <span className={`text-xs font-medium px-2 py-1 rounded ${
                          aiAnalysis.defense.winner === 'player1' ? 'bg-blue-100 text-blue-800' :
                          aiAnalysis.defense.winner === 'player2' ? 'bg-green-100 text-green-800' :
                          'bg-gray-100 text-gray-800'
                        }`}>
                          {aiAnalysis.defense.winner === 'player1' ? player1?.full_name :
                           aiAnalysis.defense.winner === 'player2' ? player2?.full_name : 'Tie'}
                        </span>
                      </div>
                      <div className="flex justify-between items-center mb-2">
                        <span className="text-sm text-muted-foreground">{player1?.full_name}</span>
                        <span className="font-bold text-blue-600 text-xs">{aiAnalysis.defense.player1Defense}</span>
                      </div>
                      <div className="flex justify-between items-center mb-2">
                        <span className="text-sm text-muted-foreground">{player2?.full_name}</span>
                        <span className="font-bold text-green-600 text-xs">{aiAnalysis.defense.player2Defense}</span>
                      </div>
                      <p className="text-xs text-muted-foreground mt-2 italic">{aiAnalysis.defense.reasoning}</p>
                    </div>

                    {/* Efficiency */}
                    <div className="bg-secondary/30 rounded-lg p-4 shadow-sm border border-border/30 md:col-span-2">
                      <div className="flex items-center justify-between mb-2">
                        <h5 className="font-semibold text-foreground flex items-center gap-2">
                          <span>⚡</span> Shooting Efficiency
                        </h5>
                        <span className={`text-xs font-medium px-2 py-1 rounded ${
                          aiAnalysis.efficiency.winner === 'player1' ? 'bg-blue-100 text-blue-800' :
                          aiAnalysis.efficiency.winner === 'player2' ? 'bg-green-100 text-green-800' :
                          'bg-gray-100 text-gray-800'
                        }`}>
                          {aiAnalysis.efficiency.winner === 'player1' ? player1?.full_name :
                           aiAnalysis.efficiency.winner === 'player2' ? player2?.full_name : 'Tie'}
                        </span>
                      </div>
                      <div className="grid grid-cols-2 gap-4 mb-2">
                        <div className="flex justify-between items-center">
                          <span className="text-sm text-muted-foreground">{player1?.full_name}</span>
                          <span className="font-bold text-blue-600">{(aiAnalysis.efficiency.player1FG * 100).toFixed(1)}%</span>
                        </div>
                        <div className="flex justify-between items-center">
                          <span className="text-sm text-muted-foreground">{player2?.full_name}</span>
                          <span className="font-bold text-green-600">{(aiAnalysis.efficiency.player2FG * 100).toFixed(1)}%</span>
                        </div>
                      </div>
                      <p className="text-xs text-muted-foreground mt-2 italic">{aiAnalysis.efficiency.reasoning}</p>
                    </div>
                  </div>

                  {/* Key Insights */}
                  <div className="bg-primary/10 rounded-lg p-4 border border-primary/20">
                    <h5 className="font-semibold text-foreground mb-3 flex items-center gap-2">
                      <span>💡</span> Key Insights
                    </h5>
                    <ul className="space-y-2">
                      {aiAnalysis.keyInsights.map((insight, index) => (
                        <li key={index} className="flex items-start gap-2 text-sm text-foreground">
                          <span className="text-primary font-bold">•</span>
                          <span>{insight}</span>
                        </li>
                      ))}
                    </ul>
                  </div>

                  {/* Model Info */}
                  {aiAnalysis.modelUsed && (
                    <div className="text-xs text-muted-foreground text-center pt-2 border-t border-border/50">
                      Analysis by: {aiAnalysis.modelUsed}
                      {aiAnalysis.fallbackTriggered && <span className="ml-2 text-yellow-600">(Fallback model used)</span>}
                    </div>
                  )}
                </div>
              </div>

              {/* Ask AI Follow-up */}
              <AskAIFollowUp
                context={{
                  type: 'player-comparison',
                  originalPrediction: aiAnalysis,
                  stats: {
                    player1,
                    player2
                  }
                }}
              />
            </div>
          )}
        </div>
      )}

      {/* Initial State */}
      {!player1 && !player2 && !loading && (
        <div className="bg-gradient-to-r from-blue-600 to-green-600 rounded-lg shadow-lg p-12 text-white">
          <div className="text-center">
            <span className="text-6xl mb-4 block">⚖️</span>
            <h3 className="text-2xl font-bold mb-3">Compare NBA Players</h3>
            <p className="text-blue-100 mb-6">
              Select two players above to see a side-by-side statistical comparison
            </p>
            <div className="flex flex-wrap justify-center gap-2">
              <span className="px-3 py-1 bg-blue-700 rounded-full text-sm">Try: LeBron vs Durant</span>
              <span className="px-3 py-1 bg-blue-700 rounded-full text-sm">Try: Curry vs Harden</span>
              <span className="px-3 py-1 bg-blue-700 rounded-full text-sm">Try: Giannis vs Embiid</span>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
